.Header {
    width: 1024px;
}

.Header-Top {
    display: flex;
}

.Header-Top-Right {
    display: flex;
    padding: 10px;
    justify-content: flex-end;
    align-items: center;
}

.Header-Top-Buttons {
    width: 60px;
    font-size: 12px;
    text-align: right;
}

.Header-First {
    background-color: #FFFEFC;
    display: flex;
}

.Header-First-Logo {
    flex-grow: 1;
}

.Header-First-SearchBar {
    flex-grow: 2;
    justify-content: center;
}

.Header-First-Menu {
    display: flex;
    font-size: 14px;
    align-items: center;
    flex-grow: 1;
    justify-content: flex-end;
}

.Header-First-Menu-Buttons {
    width: 80px;
    text-align:right;
}

.Header-Second-Menu {
    display: flex;
    font-size: 16px;
    font-weight: 500;
    align-items: center;
    flex-grow: 1;
    justify-content: flex-start;
}
.Header-Second-Menu-Buttons {
    width : 60px;
    text-align: left;
}